:root {
    --nav-height: 5rem;
}

#dark {
    
}

#light {
    --nav-bar-color: #1f4287;
    --nav-bar-logo-color: #FFF;
    --nav-bar-menu-color: #FFF;
    --nav-bar-menu-hover-bg: rgba(0, 0, 0, 0.2);
    --nav-bar-icon-color: #FFF;
    --nav-bar-icon-hover-bg: rgba(0, 0, 0, 0.2);
    --nav-bar-notification-bg: rgb(255, 106, 0);
}

nav {
    position: fixed;
    height: var(--nav-height);
    width: 100%;
    user-select: none;
    background-color: var(--nav-bar-color);
    display: flex;
    justify-content: center;
    z-index: 100;
}

#bar {
    width: min(calc(1400px + 2rem), 100%);
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo {
    cursor: pointer;
    display: flex;
    align-items: center;
    text-decoration: none;
    gap: 1rem;
}
  
.logo img {
    height: 2.5rem;
}
  
.logo span {
    font-size: 2rem;
    color: var(--nav-bar-logo-color);
}

.mid {
    height: var(--nav-height);
    display: flex;
    align-items: center;
}

.mid a {
    height: 100%;
    padding: 0 2rem;
    color: var(--nav-bar-menu-color);
    text-decoration: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
}

.mid a span {
    font-size: 1.4rem;
    letter-spacing: 0.1rem;
}

.mid a:hover {
    background-color: var(--nav-bar-menu-hover-bg);
}

.mid a.mobile {
    display: none;
}

.mobile {
    display: none;
}

.right {
    display: flex;
    gap: 0.5rem;
}

.right .icon {
    width: 1.8rem;
    height: 1.8rem;
    color: var(--nav-bar-icon-color);
}

.bar-icon {
    width: 3rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.bar-icon:hover {
    background-color: var(--nav-bar-icon-hover-bg);
}

.bar-icon .icon {
    position: relative;
}

.new {
    height: 1.1rem;
    width: 1.1rem;
    margin: -1.5rem -1.5rem 0 0;
    position: absolute;
    border-radius: 50%;
    overflow: hidden;
    background-color: var(--nav-bar-notification-bg);
    display: flex;
    align-items: center;
    justify-content: center;
}

.new span {
    font-size: 0.7rem;
    color: #000000;
}

.dropdown {
    position: relative;
    display: inline-block;
}

.user {
    padding: 0.5rem 0;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.user span {
    margin-left: 1rem;
    font-size: 1.2rem;
    letter-spacing: 0.05rem;
    color: #e0e0e0;
    transition: .3s;
}

.user-image {
    height: 3rem;
    width: 3rem;
    font-size: 1.5rem;
    border-radius: 50%;
    background-color: #04AA6D;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}

.dropdown-content {
    display: none;
    position: absolute;
    right: 0;
    background-color: #f1f1f1;
    width: 200px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 10;
}

.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {
    background-color: #ddd;
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
    display: block;
}

/* Change the background color of the dropdown button when the dropdown content is shown */
/* .dropdown:hover .user {
    background-color: #3e8e41;
} */

.hide {
    display: none;
}

@media screen and (max-width: 1100px) {
    .user span {
      display: none;
    }
  
    .mid a {
      padding: 0 1rem;
    }
}

@media screen and (max-width: 800px) {
    .logo span {
        display: none;
    }
}

@media screen and (max-width: 650px) {
    nav {
        bottom: 0;
        height: 4rem;
    }

    #bar {
        padding: 0;
    }

    .logo {
        display: none;
    }

    .right {
        display: none;
    }

    .mobile {
        display: flex;
    }

    .mid {
        width: 100%;
        height: 4rem;
    }

    .mid a.mobile {
        display: flex;
    }

    .mid a {
        width: 20%;
    }

    .mid a .icon {
        width: 1.5rem;
        height: 1.5rem;
    }

    .mid a span {
        font-size: 0.7rem;
    }

    .new {
        width: 0.5rem;
        height: 0.5rem;
        margin: -2.5rem -1.3rem 0 0;
    }
}
