.container {
    padding-top: calc(var(--nav-height) + 2rem);
    padding-bottom: 2rem;
    min-width: 100%;
    min-height: 100vh;
    display: flex;
    align-items: baseline;
    justify-content: center;
    background-color: #B4BED6;
}

.quiz-box {
    width: min(calc(1400px + 2rem), 100%);
    padding: 1rem;
    /* background-color: #5985EB; */
    background: rgba(0, 47, 164, 0.12);
    border-radius: 1rem;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    /* backdrop-filter: blur(5px); */

    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    gap: 0.5rem;
}

.time-left {
    width: 100%;
    height: 100px;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background-color: rgba(31, 66, 135, 0.3);
}

.exercises {
    width: 16.5rem;
    /* height: 100px; */
    padding: 1rem;
    background-color: rgba(49, 101, 214, 0.2);
    border-radius: 1rem;
    display: flex;
    /* justify-content: space-between; */
    flex-wrap: wrap;
    gap: 0.5rem;
}

.exercise {
    width: 2.5rem;
    height: 3rem;
    cursor: pointer;
    user-select: none;
    background-color: rgba(31, 66, 135, 0.4);
    border-radius: 0.5rem;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.exercise:hover {
    border-radius: 0.3rem;
    background-color: rgba(31, 66, 135, 1);
    color: #FFF;
}

.exercise.done {
    background-color: rgba(31, 66, 135, 0.7);
}

.exercise span {
    font-size: 1rem;
}

.exercise .mark {
    top: 0.2rem;
    right: 0.2rem;
    width: 0.8rem;
    height: 0.8rem;
    color: rgba(202, 202, 202, 0.247);
    position: absolute;
}

.exercise .mark.marked {
    color: red;
}

.exercise-box {
    flex-grow: 1;
    width: 100px;
    padding: 1rem;
    border-radius: 1rem;
    background-color: #c3cee7;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.exercise-controls {
    margin-top: 2rem;
    user-select: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.exercise-controls.only-one {
    justify-content: flex-end;
}

.exercise-controls button {
    min-width: 150px;
    cursor: pointer;
    border-radius: 2rem;
    border: 1px solid rgb(255, 255, 255);
    background-color: #537AAD;
    color: #f0f0f0;
    display: flex;
    align-items: center;
    overflow: hidden;
    transition: .3s;
}

.exercise-controls button span {
    flex-grow: 1;
    font-size: 1.1rem;
    letter-spacing: 0.1rem;
    padding: 0.4rem 0.5rem;
}

.arrow {
    width: 2rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.prev {
    position: absolute;
    left: 0.5rem;
    transition: .3s;
}

.next {
    position: absolute;
    right: 0.5rem;
    transition: .3s;
}

.exercise-controls button:hover {
    border: 1px solid #B4BED6;
    background-color: #6494d3;
}

.exercise-controls button:hover .prev {
    left: 0;
}

.exercise-controls button:hover .next {
    right: 0;
}

.exercise-controls button.hide {
    display: none;
}

.quiz-result {
    width: 100%;
    margin: 2rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.quiz-result h1 {
    color: #333;
    padding-bottom: 2rem;
}

.semi-donut {
    width: 300px;
    height: 150px;
    position: relative;
    color: #FFF;
    font-size: 20px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    box-sizing: border-box;
    overflow: hidden;
    --percentage: 0;
}

.semi-donut::after {
    content: '';
    box-sizing: border-box;
    position: absolute;
    left: 0;
    top: 0;
    width: 300px;
    height: 300px;
    border: 50px solid;
    border-color: #000 #000 #ff0 #ff0;
    border-radius: 50%;
    transform: rotate(calc(1deg * (-45 + var(--percentage) * 1.8)));
}

.demo-info {
    width: 100%;
    padding: 0 1rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.demo-info {
    font-size: 1.1rem;
    line-height: 1.5rem;
}

.demo-info ul {
    padding-left: 2rem;
    line-height: 1.8rem;
}

.demo-info ul li {
    line-height: 1.6rem;
    color: #333;
}

.quiz-demo-button {
    min-width: 150px;
    margin-top: 1rem;
    padding: 0.5rem;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 2rem;
    border: 1px solid rgb(255, 255, 255);
    background-color: #537AAD;
    color: #f0f0f0;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    transition: .3s;
}

.quiz-demo-button:hover {
    border: 1px solid #B4BED6;
    background-color: #6494d3;
}

.max600 {
    max-width: 600px;
}

.end {
    align-self: flex-end;
}

.pt2 {
    margin-top: 2rem;
}

@media screen and (max-width: 800px) {
    .exercises {
        width: 100%;
        order: -1;
    }
}

@media screen and (max-width: 650px) {
    .container {
        padding-top: 2rem;
        padding-bottom: calc(var(--nav-height) + 2rem);
    }
}
