:root {
    --mark-color-default: #5a5a5a;
    --mark-color-marked: #ff0000;

    --quiz-number-color: #000;
    --quiz-number-bg: #7997be;
    --quiz-instruction-color: #000;

    --quiz-gap-data-bg: #8d9cb9;
    --quiz-gap-word-color: #000;
    --quiz-gap-word-bg: #FFF;
    --quiz-gap-bg: #9fa7b9;

    --quiz-text-color: #000;
}

.exercise-content {
    min-height: 200px;
    display: flex;
    flex-direction: column;
}

.exercise-content .instruction {
    margin-bottom: 1rem;
    user-select: none;
    display: flex;
    gap: 0.6rem;
}

.exercise-content .instruction .number {
    min-width: 3rem;
    padding: 0.7rem 0.2rem;
    cursor: pointer;
    user-select: none;
    color: var(--quiz-number-color);
    background-color: var(--quiz-number-bg);
    border-radius: 0.5rem;
    font-size: 1.3rem;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.exercise-content .instruction .number .mark {
    top: 0.1rem;
    right: 0.1rem;
    width: 0.8rem;
    height: 0.8rem;
    color: var(--mark-color-default);
    position: absolute;
}

.exercise-content .instruction .number .mark.marked {
    color: var(--mark-color-marked);
}

.exercise-content .instruction span {
    flex-grow: 1;
    font-size: 1.2rem;
    color: var(--quiz-instruction-color);
    display: flex;
    align-items: center;
}

/* */
.content {
    margin-top: 0.5rem;
    padding: 0.5rem;
    color: var(--quiz-text-color);
    font-size: 1.1rem;
    line-height: 2.2rem;
    white-space: pre-wrap;
}

.noselect {
    user-select: none;
}

#get-answers {
    display: none;
}


/* --------------------- DRAG AND DROP --------------------- */
.gap-data {
    padding: 0.5rem;
    border-radius: 1rem;
    background-color: var(--quiz-gap-data-bg);
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
}

.gap-data::after {
    content: '.';
    padding: 0.2rem 0;
    font-size: 1.1rem;
    visibility: hidden;
}

.gap-data:not(:empty)::after {
    display: none;
}

.word {
    padding: 0.2rem 0.4rem;
    border-radius: 0.5rem;
    color: var(--quiz-gap-word-color);
    background-color: var(--quiz-gap-word-bg);
    font-size: 1.1rem;
}

.word.dragging {
    opacity: 0.3;
}

.gap {
    display: inline-block;
    line-height: 2rem;
    border-radius: 0.5rem;
    margin: 0 0.5rem;
}

.gap:empty {
    width: 6rem;
    height: 1.7rem;
    background-color: var(--quiz-gap-bg);
}

.gap:empty::after {
    content: '.';
    visibility: hidden;
}

.gap .word {
    width: 6rem;
}

.gap.disable .word {
    pointer-events: none;
}

.gap.disable .word.dragging {
    pointer-events: all;
}


/* -------------- SINGLE AND MULTIPLE CHOICE --------------- */
.choice-content {
    display: flex;
    flex-direction: column;
    /* gap: 0.2rem; */
    /* background-color: red; */
}

.choice-content label {
    padding: 0.2rem;
    cursor: pointer;
    display: flex;
    align-items: flex-start;
}

.choice-content label:hover {
    border-radius: 1rem;
    background-color: rgba(0, 0, 0, 0.05);
}

.choice-content input {
    width: 0.9rem;
    cursor: pointer;
}

.choice-content .letter {
    min-width: 3rem;
    display: flex;
    gap: 0.5rem;
}














